const initState = {
    projects: [
        { id: '1', title: 'help me find peach', content: 'blah blah blah' , name: 'Michael' ,bust: 2, url:'http://www.loadedimage.com'},
        { id: '2', title: 'collect all the stars', content: 'blah blah blah', name: 'Michael' ,bust: 2, url:'http://www.loadedimage.com' },
        { id: '3', title: 'egg hunt with yoshi', content: 'blah blah blah', name: 'Michael' ,bust: 2, url:'http://www.loadedimage.com' }
    ]
}

const projectReducer = (state = initState, action) => {
    switch (action.type) {
        case 'CREATE_PROJECT':
            console.log('created project', action.project);
            return state;
        case 'CREATE_PROJECT_ERROR':
            console.log('created project error', action.err);
            return state;
        default:
            return state;
    }
}

export default projectReducer;